import styled from "styled-components";
import Photo from "./Photo";

const PhotosGrid = styled.div `
  width: 100%;
  height: 100%;
  padding: 24px 86px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 250px 250px 250px 250px;
  justify-content: space-between;
  max-width: 1280px;
  grid-gap: 38px;
  margin: auto;
`;

function PhotoContainer(props) {
    const { photosArray, category, cart, setCart, setModal,searchText } = props;

    return ( 
        <PhotosGrid className = "photos-grid" > {
            photosArray.map((photo) => {
                const shouldAppear = (searchText === '' && (category === "all" || photo.category === category) 
                || searchText !== '' && (category === "all" || photo.category === category) && (photo.code + '').indexOf(searchText) !== -1);
                return shouldAppear ? ( 
                    <Photo url = { photo.url }
                        code = { photo.code }
                        cart = { cart }
                        setModal = { setModal }
                        setCart = { setCart }
                    />
                ) : ( 
                    <div style = {{ display: "none" }} />
                );
            })
        }
        </PhotosGrid>
    );
}

export default PhotoContainer;